@import url(https://fonts.googleapis.com/css?family=Orbitron|Roboto|Text+Me+One);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.header {
    width: 100vw;
    color: #005555;
    padding: 20px 0;
}

.header h1 {
    text-align: center;
    font-size: 25px;
}
.quote-container {
    flex: 1 1 auto;
    width: 25%;
    height: 435px;
    box-shadow: 0 0 1px 1px rgb(160, 227, 219);
    border-radius: 15px;
    text-align: center;
    margin-left: 10px;
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
    font-family: "Text Me One", sans-serif;
}

.quote-container p {
    margin: 0;
}

.quote-text {
    padding: 0 20px;
    font-size: 23px;
    color: #2c3d55;
}

.quotation-mark {
    color: #005555;
    font-size: 50px;
    font-family: serif, sans-serif, monospace;
}

.left {
    padding: 0 0 0 30px;
    text-align: left;
}

.right {
    padding: 0 30px 0 0;
    text-align: right;
}

.quote-author {
    font-family: "Roboto", sans-serif;
    font-size: 16px;
    color: #005555;
}

.quote-author:before {
    content: "-- ";
}

@media (min-width: 320px) and (max-width: 480px) {
    .quote {
        width: 100%;
        height: auto;
        margin: 0 10px;
        padding: 0 5px;
    }
    .quote p {
        padding: 0;
        font-size: 18px;
        margin: 0;
    }
    .quote p:before,
    .quote p:after {
        font-size: 25px;
        padding: 0 5px;
        margin: 10px 0 0 0;
    }
}
.btn {
    padding: 10px 0;
    min-width: 90px;
    margin: 10px;
    outline: none;
    border-radius: 8px;
    border: 1px solid transparent;
    cursor: pointer;
    -webkit-transition: all 2ms ease;
    transition: all 2ms ease;
    font-size: 18px;
    font-family: "Roboto", sans-serif;
    background-color: #e5e5e5;
}

.btn-start {
    box-shadow: 0 0 1px 1px #6dc9dd;
}

.btn-start:hover {
    box-shadow: 0 0 0 2px #024250;
}

.btn-start:active {
    background: -webkit-linear-gradient(#f4f3ea, #6dc9dd);
    background: linear-gradient(#f4f3ea, #6dc9dd);
}

.btn-stop {
    box-shadow: 0 0 1px 1px #d36582;
}

.btn-stop:hover {
    box-shadow: 0 0 2px 2px #d35682;
}

.btn-stop:active {
    background: -webkit-linear-gradient(#f4f3ea, #d36582);
    background: linear-gradient(#f4f3ea, #d36582);
}

.btn-reset {
    box-shadow: 0 0 1px 1px #b8cdf8;
}

.btn-reset:hover {
    box-shadow: 0 0 2px 2px #3c68c0;
}

.btn-reset:active {
    background: -webkit-linear-gradient(#f4f3ea, #becdf8);
    background: linear-gradient(#f4f3ea, #becdf8);
}
.settings {
  padding: 20px;
  display: flex;
  flex-flow: row wrap;
  align-items: center;
  margin: 0 auto;
  width: 90%;
}

.settings h2 {
  margin-left: 15px;
  font-size: 18px;
  width: 100%;
}

.timer-settings {
  flex: 1 1 auto;
}

.other-settings {
  flex: 1 1 auto;
}

.settings label {
  display: inline-block;
  margin: 10px;
}

.settings input[type="number"] {
  width: 40px;
  padding: 5px;
  margin-right: 15px;
  box-shadow: 0 0 1px 1px #2c3d55;
  border-radius: 3px;
  border: 0;
  font-size: 18px;
}

.settings input[type="number"]:focus {
  box-shadow: 0 0 2px 2px #2c3d55;
  outline: 0;
}

.settings input[type="checkbox"] {
  display: none;
}

.settings span {
  width: 16px;
  height: 16px;
  margin-right: 15px;
  box-shadow: 0 0 1px 1px #2c3d55;
  border-radius: 3px;
  cursor: pointer;
  position: relative;
  background-color: #ffffff;
  display: inline-block;
  vertical-align: middle;
}

.settings span:hover {
  box-shadow: 0 0 2px 2px #2c3d55;
}

.settings input[type="checkbox"]:checked + span::before {
  content: "";
  position: absolute;
  border-left: 2px solid #2c3d55;
  border-bottom: 2px solid #2c3d55;
  width: 10px;
  height: 3px;
  top: 4px;
  left: 2px;
  -webkit-transform: rotate(80deg);
  transform: rotate(315deg);
}

@media (min-width: 320px) and (max-width: 480px) {
  .settings {
    flex-flow: column wrap;
    align-items: center;
  }
  .settings h2 {
    margin: 8px;
    text-align: left;
  }
}

@font-face {
  font-family: "digital";
  src: url(/pomodoro-timer/static/media/digital-webfont.5b0f802e.woff) format("woff2"),
    url(/pomodoro-timer/static/media/digital-webfont.1edd89f1.woff2) format("woff");
  font-weight: normal;
  font-style: normal;
}

.timer-container {
  flex: 2 1 auto;
  align-self: flex-start;
  border-radius: 15px;
  box-shadow: 0 0 1px 2px rgba(102, 51, 153, 0.4);
  padding: 40px 0;
  display: flex;
  flex-flow: column wrap;
  align-items: center;
  justify-content: center;
  margin: 0 10px;
}

.timer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.clock {
  height: 200px;
  width: 200px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: "digital", Arial, Helvetica, sans-serif;
  letter-spacing: 5px;
  font-size: 45px;
  font-weight: bold;
  margin: 15px;
}

.session {
  box-shadow: 0 0 5px 10px hsla(180, 100%, 17%, 1),
    0 0 10px 5px hsla(180, 100%, 17%, 0.7);
  border: 4px solid hsla(180, 100%, 17%, 0.6);
}

.break {
  box-shadow: 0 0 0 10px #3066be, 0 0 0 5px inset #6d9dc5;
}

@media (min-width: 320px) and (max-width: 480px) {
  .clock {
    height: 180px;
    width: 180px;
    margin: 20px 40px 30px 40px;
    font-size: 40px;
    flex-flow: column wrap;
  }
  .timer-container {
    box-shadow: none;
    padding: 5px 0;
  }
  .timer {
    flex-flow: column wrap;
  }
}

.notifications {
    position: absolute;
    top: 5px;
    right: 5px;
}

.show-msg {
    -webkit-animation: show-msg 4s ease;
            animation: show-msg 4s ease;
    margin: 5px 10px;
    padding: 8px;
    width: 260px;
    text-align: center;
    background-color: #ffd900ad;
    color: black;
    border-radius: 5px;
    opacity: 0;
}

@-webkit-keyframes show-msg {
    0% {
        opacity: 1;
    }
    20% {
        opacity: 0.8;
    }
    40% {
        opacity: 0.6;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 0.2;
    }
    100% {
        opacity: 0;
    }
}

@keyframes show-msg {
    0% {
        opacity: 1;
    }
    20% {
        opacity: 0.8;
    }
    40% {
        opacity: 0.6;
    }
    60% {
        opacity: 0.4;
    }
    80% {
        opacity: 0.2;
    }
    100% {
        opacity: 0;
    }
}
.todo-container {
    align-self: flex-start;
    min-height: 425px;
    margin-right: 10px;
    flex: 1 1 auto;
    width: 28%;
    box-shadow: 0 0 1px 1px rgb(255, 255, 0);
    border-radius: 15px;
}

.todo-title {
    text-align: center;
    color: #005555;
    font-size: 25px;
}

.todo-form {
    border: 1px solid #005555;
    display: flex;
    margin: 0 auto;
    border-radius: 15px;
    width: 80%;
}

.todo-input {
    padding: 10px;
    outline: none;
    border: 0;
    color: #2c3d55;
    font-weight: bold;
    font-size: 16px;
    flex: 2 1 auto;
    border-radius: 15px;
    background: #ffffff;
}

.btn-add-todo {
    outline: none;
    border: 0;
    border-top-right-radius: 14px;
    border-bottom-right-radius: 14px;
    width: 60px;
    background-color: #005555;
    color: #ffffff;
    cursor: pointer;
}

.btn-add-todo:active {
    background: -webkit-linear-gradient(#ffffff, #3f6e9e);
    background: linear-gradient(#ffffff, #3f6e9e);
}

.todo-filter {
    text-align: center;
}

.todo-filter input[type="button"] {
    border: 0;
    outline: 0;
    margin: 3px;
    padding: 3px 10px;
    font-size: 15px;
    cursor: pointer;
}

.todo-items li {
    list-style-type: none;
    padding: 9px;
    font-size: 16px;
    margin: 5px;
}

.todo-items li:hover {
    box-shadow: 0 0 1px 3px #f4f3ea;
    border-radius: 5px;
    background-color: #eaebf4;
    padding: 10px 12px;
}

.completed-task {
    text-decoration: line-through;
    -webkit-text-decoration-color: "#005555";
            text-decoration-color: "#005555";
}

.todo-items input[type="checkbox"] {
    display: none;
}

.todo-items span {
    box-shadow: 0 0 1px 1px #005555;
    border-radius: 3px;
    position: relative;
    margin-right: 10px;
    height: 16px;
    width: 16px;
    display: inline-block;
    vertical-align: middle;
}

.todo-items input[type="checkbox"]:checked+span {
    background-color: #005555;
}

.todo-items input[type="checkbox"]:checked+span::before {
    content: "";
    position: absolute;
    border-left: 2px solid #ffffff;
    border-bottom: 2px solid #ffffff;
    width: 10px;
    height: 3px;
    top: 4px;
    left: 2px;
    -webkit-transform: rotate(80deg);
    transform: rotate(315deg);
}

.remove-btn {
    float: right;
    opacity: 0;
}

.todo-items li:hover .remove-btn {
    opacity: 1;
    background: red;
}

.no-tasks {
    text-align: center;
    padding: 50px 0;
}

@media (min-width: 320px) and (max-width: 480px) {
    .todo-container {
        box-shadow: none;
    }
    .todo-form {
        width: 90vw;
    }
    .todo-items {
        padding-left: 10px;
        margin: 0 auto;
    }
}
body {
    padding: 0;
    background: #f4f3ea;
    color: #41463d;
    font-family: "Roboto", sans-serif;
}

.container {
    display: flex;
    flex-flow: row wrap;
}

@media (min-width: 320px) and (max-width: 480px) {
    body {
        background-color: #ffffff;
    }
}
